@use "config" as *;
.page-nav {

   @include media ('<=phone') {
      display: none;
   }



   

   @include media ('>phone') {
      display: flex;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      padding-left: var(--padding-aside);
      z-index: 9000;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: rem(100);
      width: var(--size-nav-icon);
      box-sizing: content-box;
   }

   @media (max-height: 500px) and (orientation: landscape) { 
      display: none;
   }

		&__arrow {
         --arrow-width: 4rem !important;
		}

		&__arrow--up {
         transform: rotate(90deg);
		}

		&__arrow--down {
         transform: rotate(-90deg);
		}
}